'use client';

import { useEffect, useState } from 'react';

export const useIsMobile = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkIsMobile = () => {
      // ÌôîÎ©¥ ÎÑàÎπÑÍ∞Ä 768px Ïù¥ÌïòÏù¥Í±∞ÎÇò, User-AgentÍ∞Ä Î™®Î∞îÏùºÏù∏ Í≤ΩÏö∞
      const isMobileByWidth = window.innerWidth <= 768;
      const isMobileByUserAgent = /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(
        navigator.userAgent
      );
      
      setIsMobile(isMobileByWidth || isMobileByUserAgent);
    };

    // Ï¥àÍ∏∞ Ï≤¥ÌÅ¨
    checkIsMobile();

    // Î¶¨ÏÇ¨Ïù¥Ï¶à Ïù¥Î≤§Ìä∏ Î¶¨Ïä§ÎÑà Ï∂îÍ∞Ä
    window.addEventListener('resize', checkIsMobile);

    return () => {
      window.removeEventListener('resize', checkIsMobile);
    };
  }, []);

  return isMobile;
}; 